import React from "react";
import { RawHTML } from 'react-dom';

/**
 * This is just a conveniance file
 * to get all texts used into views in the same file
 * It could be also useful if i want to add i18n feature
 */
const data = {
  blog: {
    title: "Articles de blog",
  },
  global: {
    copyright: "© 2021 Aurélien Dudonney",
  },
  errors: {
    notfoundTitle: "404",
    notfoundText: "L'adresse demandé de produit pas de résultat.",
    notfoundAction: "accéder à la page d'accueil",
  },
};


function getText (name, domain) {
  if (
    domain &&
    Object.prototype.hasOwnProperty.call(data, domain) &&
    Object.prototype.hasOwnProperty.call(data[domain], name)
  ){
    return data[domain][name]
  }

  else if (name && Object.prototype.hasOwnProperty.call(data, name)) {
    return data[name]
  }

  return ""
}

export { getText as default }